<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 19,  //当前表格的名称编号
        setting: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.setting = this.$store.state.companyCurrent.content.page19
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第4列运算
        // this.fn2(4, 6, [2, 3], 2)
        //第9列运算
        // this.fn2(9, 4, [5, 6, 7, 8], 1)
        //第7行12列计算
        this.fn1('7_12', ['2_12', '3_12', '4_12', '5_12', '6_12'], 1)
      },
      // q 要计算单元格的name
      // jump 参与运算的单元格name集合
      // type {
      // 1:a=b+c...
      // }
      fn1(q, jump, type) {
        if (J('input[name=' + jump[0] + ']').val()) {
          var sum = parseFloat(J('input[name=' + jump[0] + ']').val())
        } else {
          var sum = 0
        }
        for (let i = 1; i < jump.length; i++) {
          let dom = J('input[name=' + jump[i] + ']')
          if (dom.val()) {
            if (type == 1) {
              sum += parseFloat(dom.val())
            }
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      },
      // q 要运算的列
      // r 要运算多少行
      // jump 参与运算的列
      // type 运算区分
      // 1:第9列运算
      // 2:第4列运算
      fn2(q, r, jump, type) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          if (type == 1) {
            for (let k = 0; k < jump.length; k++) {
              let dom = J('input[name=' + i + '_' + jump[k] + ']')
              if (dom.val()) {
                sum += parseFloat(dom.val())
              }
            }
            J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
          } else if (type == 2) {

            if (J('input[name=' + i + '_' + jump[0] + ']').val()) {
              var dom2 = parseFloat(J('input[name=' + i + '_' + jump[0] + ']').val())
            } else {
              var dom2 = 0
            }
            if (parseFloat(J('input[name=' + i + '_' + jump[1] + ']').val())) {
              var dom3 = parseFloat(J('input[name=' + i + '_' + jump[1] + ']').val())
            } else {
              var dom3 = 0
            }
            if (dom2 < 0) {
              J('input[name=' + i + '_' + q + ']').val((dom2 + dom3).toFixed(2))
            } else {
              J('input[name=' + i + '_' + q + ']').val(dom3.toFixed(2))
            }
          }
        }
      }
    }
  }
</script>


<style scoped>
  table, table tr th, table tr td {
